import './style.css';
import { sample } from '../../sample';
import CoinStats from '../../components/CoinStats/CoinStats';
import Search from '../../components/Search/Search';
import CoinDetail from '../../components/CoinDetail/CoinDetail';
import BrowsingHistory from '../../components/BrowsingHistory/BrowsingHistory';
import { useEffect, useState } from 'react';

function Main() {
  const [data, setData] = useState(sample);
  const [detailDisplay, setDetailDisplay] = useState(sample[0]);
  const [browseHistory, setBrowseHistory] = useState([sample[0]]);

  useEffect(() => {
    getCoinMarketData();
    setInterval(getCoinMarketData, 60000);
  }, []);

  async function getCoinMarketData() {
    const getAllCoinsDataURL = 'https://api.coinpaprika.com/v1/tickers';
    const response = await fetch(getAllCoinsDataURL);
    const result = await response.json();
    setData(result.slice(0, 10));
    setDetailDisplay(result[0]);
  }

  async function getCoinMarketDataById(id) {
    const getCoinDataByIdURL = `https://api.coinpaprika.com/v1/tickers/${id}`;
    const response = await fetch(getCoinDataByIdURL);
    const result = await response.json();
    setDetailDisplay(result);
    
    let inHistory = browseHistory.find(item => item.id === id);
    if (!inHistory) {
      setBrowseHistory(browseHistory.concat(result));
    }
  }

  return ( 
    <main>
      <CoinStats 
        data={data}
        columns={["Rank", "Name", "Price", "24h Change"]}
      />
      <Search handleShowDetail={getCoinMarketDataById} />
      <CoinDetail coin={detailDisplay}/>
      <BrowsingHistory
        browseHistory={browseHistory} 
        handleShowDetail={getCoinMarketDataById}
      />
    </main>
  );
}

export default Main;