import BrowsingHistoryItem from './BrowsingHistoryItem';
import './style.css';

function BrowsingHistory({ browseHistory ,handleShowDetail }) {
  return (  
    <div id="browsing-history">
      <h3>Browsing History</h3>
      <div className='browing-history-container'>
        {browseHistory.map(item => (
          <BrowsingHistoryItem 
            key={item.id}
            id={item.id}
            name={item.name} 
            symbol={item.symbol}
            price={item.quotes.USD.price}
            change={item.quotes.USD.percent_change_24h}
            handleOnClick={() => handleShowDetail(item.id)}
          />
        ))}
      </div>
    </div>
    
  );
}

export default BrowsingHistory;